<template>
	<view class="content">
		<view class="top">

		</view>
		<!-- 顶部tbas -->
		<scroll-view scroll-x="true" class="classification">
			<view class="classbox">
				<view @click="tabs(item,index)" :class="[{'boxs':class_index==index},'box']"
					v-for="(item,index) in $t('index.classification')">
					{{item.name}}
				</view>
			</view>
		</scroll-view>
		<!-- 分类 -->
		<view class="mo_class">
			<view class="center">
				<p @click="mo_change(item,index)" v-for="(item,index) in $t('commodity.mo_class')"
					:class="[{'cli':mo_index==index},'name']">
					{{item}}
					<view class="san" v-if="index == 3">
						<image src="https://www.zhahuodi.com/uploads/20230512/53973dc734813dbb84fd434ced071b18.png"
							mode="widthFix"></image>
						<image src="https://www.zhahuodi.com/uploads/20230512/0f69034dad4b049c85441fc7b2dbdc49.png"
							mode="widthFix"></image>
					</view>

					<view class="san saa" v-if="index == 4">
						<image src="https://www.zhahuodi.com/uploads/20230512/0f69034dad4b049c85441fc7b2dbdc49.png"
							mode="widthFix"></image>
					</view>
				</p>
			</view>
		</view>
		<!-- 所有商品 -->
		<view class="mo1">
			<view class="mo1_box" v-for="(item,index) in 10">
				<view class="mo1_imgbox">
					<u--image :showLoading="true" :src="src" width="236rpx" height="252rpx"></u--image>
				</view>
				<view class="mo1_nr">
					<view class="title">标题标题标题标题标题标题标题题标题标题标题标题标题标题标题标题题标题</view>
					<view class="jd">
						<u-line-progress percentage="50" :showText='false' :striped="true" height='8rpx'
							active-color="#ff5152"></u-line-progress>
					</view>
					<view class="nr">
						<p><span class="sp">0</span> {{$t('commodity.participated')}}</p>
						<p><span class="sps">3</span> {{$t('commodity.remaining')}}</p>
					</view>
					<view class="num">
						<p class="num_p">{{$t('commodity.participateds')}} <span>1</span> {{$t('commodity.times')}}</p>
						<view class="mo1_btn">
							<image src="../../static/q50@2x.png" mode="widthFix"></image>
							{{$t('commodity.bay')}}
						</view>
					</view>
					
				</view>
				
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				rtl: '',
				src: '',
				class_index: 1,
				mo_class: ['即将揭晓', '人气', '最新', '价格', '商品分类'],
				mo_index: 0
			};
		},
		onShow() {

		},
		methods: {
			mo_change(e, i) {
				console.log(e, i);
				this.mo_index = i
			},
			tabs(e, i) {
				console.log(e, i);
				if (i == 0) {
					uni.switchTab({
						url: '/pages/index/index'
					})
				}
			},
		}
	}
</script>

<style lang="scss">
	.content {
		.top {
			width: 750rpx;
			height: 88rpx;
			position: relative;

			.locale {
				width: 100rpx;
				height: 88rpx;
				position: absolute;
				right: 0;
				top: 0;
				display: flex;
				align-items: center;
				// background-color: sk;
				justify-content: center;
			}
		}

		.classification {
			width: 750rpx;
			height: 88rpx;

			.classbox {
				display: flex;
				align-items: center;
				padding-left: 38rpx;

				.box {
					display: flex;
					align-items: center;
					justify-content: center;
					margin-right: 16rpx;
					background-color: #eeeeee;
					padding: 0 32rpx;
					color: #666666;
					height: 58rpx;
					line-height: 28rpx;
					flex-shrink: 0;
					font-weight: 400;
					border-radius: 40rpx;
				}

