							span{
								color: #FF5152;
								margin: 0 5rpx;
							}
						}
						
						.mo1_btn {
							padding: 0 20rpx;
							// width: 112rpx;
							height: 44rpx;
							background: #FF5152;
							border-radius: 22rpx 22rpx 22rpx 22rpx;
