							}
						}
					}
					.nr {
						display: flex;
						align-items: center;
						justify-content: space-between;
						font-size: 20rpx;
						font-weight: 400;
						color: #666666;
						margin-top: 8rpx;

						.sp {
							color: #FF5152;
							margin: 0 5rpx;
						}

						.sps {
							color: #5BB6EA;
