						width: 430rpx;
						font-size: 32rpx;
						line-height: 38rpx;
						word-break:break-all;
						display: -webkit-box;
						-webkit-line-clamp:2;
						-webkit-box-orient: vertical;
						overflow: hidden;
					}
				}
			}
		}

		.mo_class {
			width: 750rpx;
			height: 100rpx;
			background: linear-gradient(180deg, #FFF1F1 0%, rgba(255, 248, 248, 0.37) 100%);
			border-radius: 40rpx 40rpx 0rpx 0rpx;
			color: #FF5152;
			line-height: 28rpx;
			font-size: 24rpx;

			.center {
				height: 100rpx;
				width: 90%;
				margin: 0 auto;
				display: flex;
				align-items: center;
				justify-content: space-between;

				.name {
					display: flex;
					height: 100%;
					text-align: center;
					line-height: 100rpx;
					position: relative;

					.san {

						margin-left: 5rpx;
						margin-top: 4rpx;
						display: flex;
						align-items: center;
						justify-content: center;
						flex-direction: column;
						height: 100rpx;

						image {
							width: 7px;
							margin: 2rpx 0;
							height: auto;
						}
					}

					.saa {

						image {
							margin-top: 6rpx;
							width: 7px;
							height: auto;
						}
					}
				}

				.cli {
					color: #FF5152;
				}

				.cli::before {
					content: '';
					width: 86rpx;
					height: 0rpx;
					position: absolute;
					left: 50%;
					bottom: 10rpx;
					margin-left: -43rpx;
					border: 3rpx solid #FF5152;
					border-radius: 4px;
				}
			}
		}
